<template>
 <div class="dom">
   <div class="book" :style="{'height':heightBox}">
     qiaojie
   </div>
 </div>
</template>

<script type="text/ecmascript-6">
export default {
  name: 'dom',
  data() {
    return {
      ck: document.body.clientHeight
    }
  },
  computed: {
    heightBox() {
      const _this = this
      window.addEventListener('resize', () => {
        _this.ck = document.body.clientHeight
      })
      return this.ck - 300 + 'px'
    }
  },
  methods: {}
}
</script>

<style scoped lang="stylus">
.book
  background red
</style>
